import React, { useContext, useEffect, useState } from 'react';
import { GeoJSON } from 'react-leaflet';
import { Feature, LineString } from 'geojson';
import { GeoJSONFileNode } from '../types';
import { ActiveEntryContext, isActiveEntry } from '../ActiveEntryContext';
import loadable from '@loadable/component';
import { useIsMobile } from '../mediaQueries';
const Elevation = loadable(() => import('./Elevation'));

export default function GeoJSONForUrl({ publicURL, name }: GeoJSONFileNode) {
  const [geoJson, setGeoJson] = useState<Feature<LineString>>();
  const { setActiveEntry } = useContext(ActiveEntryContext)!;
  const weight = useIsMobile() ? 5 : 3;
  const [date, _activityType] = name.split('_');
  const isActive = isActiveEntry(date) && geoJson?.properties?.activityType === 'hiking';
  useEffect(() => {
    fetch(publicURL)
      .then(resp => resp.json())
      .then(setGeoJson);
  }, [publicURL]);
  return geoJson &&
    (
      (isActive)
        ? <Elevation data={geoJson} />
        : <GeoJSON data={geoJson} style={{ color: 'gray', weight }} eventHandlers={{
          click: () => { setActiveEntry({ date, forceScroll: true }); },
        }} />
    );
}

